(function ()
{
    'use strict';

    angular
    	.module('app.press.licenses')
        .controller('PressLicensePackageDialogController', PressLicensePackageDialogController);

    /** @ngInject */
    function PressLicensePackageDialogController($translate, $state, $mdDialog, RestService, PATH)
    {
        var vm = this;

        // Data
        vm.req = {
        	template: null,
        	exam: null,
        	qtd: 1,
        	per: 50,
        	name: null,
        	reseller: null
        }
        
        vm.resellers = [];

        // Methods
        vm.cancel = cancel;
        vm.next = next;
        vm.changeTemplate = changeTemplate;
        
        init();

        // ////////
        
        /**
		 * Initialize
		 */
        function init()
        {
			vm.req.exam = null;
			RestService.get({ url: PATH.rest + 'press/examtemplates' })
			.then(function(response) {
				vm.templates = response.data;
			});
			RestService.get({ url: PATH.rest + 'press/resellers' })
			.then(function(response) {
				vm.resellers = response.data;
			});
        }

        function changeTemplate()
        {
        	if (vm.req.template.exams.length > 0) {
    			vm.req.exam = vm.req.template.exams[0];
        	}
        }

        /**
		 * Next
		 */
        function next()
        {
        	var req = {
        		product: {
        			id: vm.req.exam.id
        		},
        		seller: {
        			id: vm.req.reseller.id,
        		},
        		owner: null,
        		nrQtd: vm.req.qtd,
        		nrPer: vm.req.per,
        		state: null,
        		nmLicensePackage: vm.req.name
        	};
			
			RestService.post({
				url: PATH.rest + 'press/licensepackage/batch',
				data: req
			}).then(function(response) {
	            $mdDialog.hide(response.data);
			});

        }

        /**
		 * Cancel
		 */
        function cancel()
        {
            $mdDialog.cancel();
        }
        
    }
})();

