(function() {
	'use strict';

	angular
		.module('app.reseller.licenses')
		.controller('ResellerLicensesController', ResellerLicensesController);

	/** @ngInject */
	function ResellerLicensesController($state, $translate, AuthService, RestService, PATH, $mdDialog, $document) {

		var api = {};
		var vm = this;
		
		// Data
//		   0        1      2   3
//		name,versions,update,but
		vm.items = {};

		vm.dtInstance = {};
		vm.dtOptions = {
			dom : 'rt<"bottom"<"left"<"length"l>><"right"<"info"i><"pagination"p>>>',
			columnDefs : [
					{
						targets : 0,
						filterable : true,
						sortable : true
					}, {
						targets : 1,
						filterable : false,
						sortable : false
					}, {
						targets : 2,
						filterable : true,
						sortable : true
					}, {
						// Target the actions column
						targets : 3,
						responsivePriority : 1,
						filterable : false,
						sortable : false
					} ],
			initComplete : function() {
				var api = this.api(), searchBox = angular.element('body').find(
						'#persons-search');

				// Bind an external input as a table wide search box
				if (searchBox.length > 0) {
					searchBox.on('keyup', function(event) {
						api.search(event.target.value).draw();
					});
				}
			},
			pagingType : 'numbers',
			lengthMenu : [ 10, 20, 30, 50, 100 ],
			pageLength : 20,
			scrollY : 'auto',
			responsive : true,
			language : {
				info : "_START_ - _END_ / _TOTAL_"
			}
		};

		// Methods
		vm.detail = detail;
		vm.add = add;
		vm.remove = remove;
		vm.init = init;
		
		vm.init();

		// ////////

		/**
		 * Go to product detail
		 * 
		 * @param id
		 */
		function detail(o) {
			$state.go('app.reseller_licenses.detail', {
				id : o.id
			});
		};
				
		function add(e) {
			
            $mdDialog.show({
                controller         : 'PressLicensePackageDialogController',
                controllerAs       : 'vm',
                templateUrl        : 'app/main/reseller/licenses/dialogs/package-new-form/package-new-form-dialog.html',
                parent             : angular.element($document.body),
                targetEvent        : e,
                clickOutsideToClose: false,
                locals             : {
                    event          : e
                }
            }).then(function(data) {
				vm.items.push(data);
			});
			
		};
		
		function init() {
			        	
			RestService.get({
				url: PATH.rest + 'reseller/licensepackages'
			}).then(function(response) {
				vm.items = response.data;
			});

		};

		function remove($event, $index, p) {
			
			var confirm = $mdDialog.confirm()
				.title($translate.instant('GLOBAL.DIALOG.REMOVE.TITLE'))
				.textContent($translate.instant('GLOBAL.DIALOG.REMOVE.CONTENT'))
				.ariaLabel($translate.instant('GLOBAL.DIALOG.REMOVE.CONTENT'))
				.targetEvent($event)
				.clickOutsideToClose(true)
				.parent(angular.element(document.body))
				.ok($translate.instant('GLOBAL.DIALOG.REMOVE.CANCEL'))
				.cancel($translate.instant('GLOBAL.DIALOG.REMOVE.REMOVE'));
			
			$mdDialog.show(confirm).then(function() {
				
			}, function() {
	
				RestService.remove({
					url: PATH.rest + 'reseller/licensepackage/' + p.id
				}).then(function() {
					vm.items.splice($index, 1);
					
					$mdToast.show(
							$mdToast.simple()
								.textContent($translate.instant('GLOBAL.DIALOG.REMOVE.SUCCESS'))
						    );
				});
				
			});	
			
		}
		
	}
})();

