(function ()
{
    'use strict';

    angular
    	.module('app.psychologist.applications')
        .controller('PsychologistNewPatientDialogController', PsychologistNewPatientDialogController);

    /** @ngInject */
    function PsychologistNewPatientDialogController(
			$state,
			$translate,
			RestService,
			PATH,
			$mdDialog,
			$document,
			$scope,
			Valid,
			event,
			patient,
			text,
			PostalCodeService,
			$mdToast
		) {

    	$scope.dtBirthday = null;

		var vm = this;
		var neededDocuments = { '1002': 'CPF', '1003': 'RG' };
		
		vm.search = {
			text    : null,
			patients: null
		}
		
		vm.patient = null;
		vm.isNew = !patient;
		
		vm.searchPatients = function() {
        	vm.search.patients = [];

   			RestService.get({
   				url: PATH.rest + 'psychologist/patients'
   			}).then(function(response) {
   				vm.search.patients = response.data;
   				vm.search.patients.map( function (patient) {
   					patient.value = patient.nmPatient.toLowerCase();
   					angular.forEach(patient.documents, function(document, index) {
       					patient.value += " " + document.nmPrincipal;
   					});
   					return patient;
   				});
   			}, function(response) {
   			});
		};
		
		vm.searchPatient = function(id) {
			RestService.get({
				url: PATH.rest + 'psychologist/patient/' + id
			}).then(function(response) {
				vm.refresh(response.data);
			}, function(response) {
			});

		};
		
		vm.newPatient = function(text) {
			var patient = {
					nmPatient: isCPF ? '' : text,
					documents: [
						{
							type: {
								id: 1002
							},
							nmPrincipal: isCPF ? text : ''
						}
					]
			};
			vm.refresh(patient);
		}
		
		$scope.cnpjValidation = (function() {
			return {
		    	test: function(value) {
		    		vm.corporate.name = null;
		    		vm.corporate.disabledName = true;
		        	if (!Valid.cnpj(value)) return false;
		        	
		        	var $cnpj = value.replace(/\D/g,'');
		        	
					RestService.get({
						url: PATH.rest + 'corporate/doc/CNPJ/' + $cnpj,
					}).then(function(response) {
						var corporate = response.data;
						var hasUser = corporate != null && corporate != "";
						vm.corporate.disabledName = hasUser;
						vm.corporate.id   = hasUser ? corporate.id          : null;
						vm.corporate.name = hasUser ? corporate.nmCorporate : null;
					}, function(response) {
					});
					return true;
		        }
		    };
		})();
		
		vm.selectPatient = function(i) {
			if (i) {
				vm.refresh(i);
				vm.patient = i;
			}
		}

        // Methods
        vm.cancel = function() {
        	$mdDialog.cancel();
        };

		vm.createFilterFor = function(query) {
			var lowercaseQuery = angular.lowercase(query);
			return function filterFn(patient) {
				return (angular.lowercase(patient.value).indexOf(lowercaseQuery) != -1);
			};
		}
		
		vm.querySearch = function(query) {
			var results = query
					? vm.search.patients.filter( vm.createFilterFor(query) )
					: vm.search.patients;
			return results;
		}
        
        vm.save = function() {

        	delete vm.patient.value;
//			angular.forEach(vm.patient.documents, function(document, index) {
//				if (!document.nmPrincipal) {
//	 				delete document;
//				}
//			});
        	
        	var success = function(response) {
				$mdToast.show(
						$mdToast.simple()
							.textContent($translate.instant('GLOBAL.DIALOG.SAVE.SUCCESS'))
					    );
	            $mdDialog.hide(response.data);
    		};
        	if (vm.patient.id) {
        		RestService.put({
        			url: PATH.rest + 'psychologist/patient',
        			data: vm.patient
        		}).then(success);
        	} else {
        		RestService.post({
        			url: PATH.rest + 'psychologist/patient',
        			data: vm.patient
        		}).then(success);
        	}
        	
        }		

        vm.changePostalCode = function($addr) {
        	var $postalcode = $addr.nmPostalCode.replace(/\D/g,'');
        	if ($postalcode.length == 8) {
        		PostalCodeService.cep($postalcode).then(function(response) {

        			var $found = response.data;
        			
        			$addr.city = null;
        			$addr.nmPublicPlace = null;
        			$addr.nmNeighborhood = null;
        			
        			RestService.get({ url: PATH.rest + 'geo/city/localcode/' + $found.ibge })
        			.then(function(response) {
        	        	$addr.nmPostalCode = $found.cep;
        				$addr.city = response.data;
        				$addr.nmPublicPlace = $found.logradouro;
        				$addr.nmNeighborhood = $found.bairro;
        				
        				vm.patient.address = $addr;
        			});
        			
        		});
        	}
        }

		vm.init = function() {
			if (patient) {
				this.searchPatient(patient.id);
			} else {
				var isCPF = Valid.cpf(text);
				var p = {
					nmPatient: isCPF ? '' : text,
					documents: [
						{
							type: {
								id: 1002
							},
							nmPrincipal: isCPF ? text : ''
						}
					]
				};
				vm.refresh(p);
			}
		}
        
		vm.refresh = function(p) {
			
			var neededDocuments = { '1002': 'CPF', '1003': 'RG' };
			
			if (!p.documents) {
				p.documents = [];
			}
			for (var i = 0; i < p.documents.length; i++) {
				var id = p.documents[i].type.id;
				if (neededDocuments[id]) {
					delete neededDocuments[id];
				};
			}
			
			for (var k in neededDocuments) {
				var doc = {
					type: {
						id: k
					}
				};
				p.documents.push(doc);
			}
			vm.patient = p;
			var m = moment(vm.patient.dtBirthday);
	    	$scope.dtBirthday = m.isValid() ? m.toDate() : null;

		}

		
        // ////////
        /**
		 * Initialize
		 */
        vm.init();
        
        
    }
})();

(function ()
{
    'use strict';

    angular
    	.module('app.psychologist.applications')
        .controller('PsychologistApplicationAttemptDialogController', PsychologistApplicationAttemptDialogController);

    /** @ngInject */
    function PsychologistApplicationAttemptDialogController(
			$state,
			$translate,
			RestService,
			PATH,
			$mdDialog,
			$document,
			$scope,
			Valid,
			Converter,
			event,
			application,
			attempt,
			editable,
			$mdToast,
			$sce
		) {

		var url = self.URL || self.webkitURL || self;

		var vm = this;
		
		vm.idAttempt = attempt.id;
		vm.attempt = attempt;
		vm.feedback = null;
		vm.editable = editable;

        // Methods
        vm.close = function() {
        	$mdDialog.cancel();
        };
        
        vm.account = function() {
        	
        	vm.attempt.vrHits = 0;
        	vm.attempt.vrMisses = 0;
        	vm.attempt.vrOmissions = 0;
        	
			angular.forEach(vm.attempt.points, function(p, index) {
				if (p.examPoint.vrScore == 1 && p.vrScore == 1) {
					vm.attempt.vrHits++;
				} else if (p.examPoint.vrScore == 1 && p.vrScore == 0) {
					vm.attempt.vrOmissions++;
				} else if (p.examPoint.vrScore == 0 && p.vrScore == 1) {
					vm.attempt.vrMisses++;
				}
			});
			
			vm.attempt.vrTotalErrors = vm.attempt.vrMisses + vm.attempt.vrOmissions;
			vm.attempt.vrTotalHits = vm.attempt.vrHits - vm.attempt.vrTotalErrors;
			vm.attempt.vrPercent = vm.attempt.vrHits / (vm.attempt.vrHits + vm.attempt.vrTotalErrors);
        	
        };

        vm.save = function() {

    		RestService.put({
    			url : PATH.rest + 'psychologist/application/attempt/' + vm.idAttempt + '/points',
    			data: vm.attempt.points
    		}).then(function(response) {
				var att = response.data;
				if (att.content && att.content.bnContent) {
    				var ablob = Converter.base64toBlob(
    						att.content.bnContent,
    						att.content.mimeType.nmMimeType
    				);
    				att.blob = $sce.trustAsResourceUrl(        				
    	    			url.createObjectURL(ablob)
        			);
				}

				vm.refreshAttempt(att);
				$mdToast.show(
					$mdToast.simple()
						.textContent($translate.instant('GLOBAL.DIALOG.SAVE.SUCCESS'))
				    );
    		});
        	
        }		

		vm.init = function() {

			RestService.get({
				url: PATH.rest + 'psychologist/exam/' + application.license.licensePackage.product.id + '/feedback'
			}).then(function(response) {
				vm.feedback = response.data;
				vm.acquireAttempt();
			}, function(response) {
			});
			
		}
		
		vm.acquireAttempt = function() {

			RestService.get({
				url: PATH.rest + 'psychologist/application/attempt/' + vm.idAttempt
			}).then(function(response) {
				
				var att = response.data;
				if (att.content && att.content.bnContent) {
    				var ablob = Converter.base64toBlob(
    						att.content.bnContent,
    						att.content.mimeType.nmMimeType
    				);
    				att.blob = $sce.trustAsResourceUrl(        				
    	    			url.createObjectURL(ablob)
        			);
				}

				vm.refreshAttempt(att);
				
			}, function(response) {
			});
			
		}
		
		vm.refreshAttempt = function(att) {
			
			var mapAttemptPoinst = {};
			if (att.points === null) {
				att.points = [];
			} else {
				
				angular.forEach(att.points, function(p, index) {
					mapAttemptPoinst[p.examPoint.id] = p;
				});
				
			}

			angular.forEach(vm.feedback.points, function(fp, index) {
				var fpoint = mapAttemptPoinst[fp.id];
				if (fpoint) {
					
					fpoint.examPoint = fp;
					
				} else {
					
					var npoint = {
						examPoint: fp,
						corrector: {
							id: 1 // manual
						},
						vrScore: null
					};
					
					att.points.push(npoint);
				}
			});

			vm.attempt = att;
			vm.account();

		}
		
		vm.selectPoint = function(p) {
			p.vrScore = (p.vrScore == null || p.vrScore == 0) ? 1 : 0;
			vm.account();
		}
		
		
        // ////////
        /**
		 * Initialize
		 */
        vm.init();
        
        
    }
})();

