(function ()
{
    'use strict';

    angular
    	.module('app.psychologist.applications')
        .controller('PsychologistApplication20FiguraDialogController', PsychologistApplication20FiguraDialogController);

    /** @ngInject */
    function PsychologistApplication20FiguraDialogController(
			$state,
			$translate,
			RestService,
			PATH,
			$mdDialog,
			$document,
			$scope,
			Valid,
			Converter,
			event,
			application,
			attempt,
			editable,
			$mdToast,
			$sce
		) {

		var url = self.URL || self.webkitURL || self;

		var vm = this;
		
		vm.idAttempt = attempt.id;
		vm.attempt = attempt;
		vm.editable = editable;
		vm.mapPoints = {};
		vm.ready = false;
		vm.saved = false;
		
        // Methods
        vm.close = function() {
        	if (vm.saved) {
            	$mdDialog.hide();
        	} else {
            	$mdDialog.cancel();
        	}
        };
        
        vm.account = function() {
        	
        	vm.attempt.vrHits = 0;
        	var totalone = 0;
        	
			angular.forEach(vm.attempt.points, function(p, index) {
				if (p.examPoint.vrScore == 1) {
					totalone++;
					if (p.vrScore == 1) {
						vm.attempt.vrHits++;
					}
				}
			});
			
			vm.attempt.vrTotalHits = vm.attempt.vrHits;
			vm.attempt.vrErrors = totalone - vm.attempt.vrHits;
			vm.attempt.vrPercent = vm.attempt.vrHits / totalone;
        	
        };

        vm.save = function() {
        	
        	var $d = [];
			angular.forEach(vm.attempt.points, function(p, index) {
				var np = {
					id: p.id,
					vrScore: p.vrScore,
					nmComment: p.nmComment,
					examPoint: {
						id: p.examPoint.id
					},
					corrector: {
						id: p.corrector.id
					}
				};
				$d.push(np);
			});
        	
        	
    		RestService.put({
    			url : PATH.rest + 'psychologist/application/attempt/' + vm.idAttempt + '/results',
    			data: $d
    		}).then(function(response) {
				vm.saved = true;
				$mdToast.show(
					$mdToast.simple()
						.textContent($translate.instant('GLOBAL.DIALOG.SAVE.SUCCESS'))
				    );
			}, function(response) {
			});
        	
        }		

		vm.init = function() {

			RestService.get({
				url: PATH.rest + 'psychologist/application/attempt/' + vm.idAttempt + '/feedback'
			}).then(function(response) {
				
				var att = response.data;
				if (att.content && att.content.bnContent) {
    				var ablob = Converter.base64toBlob(
    						att.content.bnContent,
    						att.content.mimeType.nmMimeType
    				);
    				att.blob = $sce.trustAsResourceUrl(        				
    	    			url.createObjectURL(ablob)
        			);
				}

				vm.attempt = att;
				vm.mapPoints = {};
				
				angular.forEach(vm.attempt.points, function(p, index) {
					vm.mapPoints[p.examPoint.idLocal] = p;
				});

				vm.account();
				vm.ready = true;
				
			}, function(response) {
			});
			
		}
		
		vm.selectPoint = function(p) {
			var ini = p.examPoint.idLocal - Math.floor((p.examPoint.idLocal - 1) % 4);
			var state = (p.vrScore == null || p.vrScore == 0) ? 1 : 0;
			
			vm.mapPoints[ini + 0].vrScore = 0;
			vm.mapPoints[ini + 1].vrScore = 0;
			vm.mapPoints[ini + 2].vrScore = 0;
			vm.mapPoints[ini + 3].vrScore = 0;
			
			p.vrScore = state;
			
			vm.account();
		}
		
		
        // ////////
        /**
		 * Initialize
		 */
        vm.init();
        
        
    }
})();

(function ()
{
    'use strict';

    angular
    	.module('app.psychologist.applications')
        .controller('PsychologistApplication20FiguraReportDialogController', PsychologistApplication20FiguraReportDialogController);

    /** @ngInject */
    function PsychologistApplication20FiguraReportDialogController(
			$state,
			$translate,
			RestService,
			PATH,
			$mdDialog,
			$document,
			$scope,
			Valid,
			event,
			application,
			attempt,
			$mdToast,
			$window
		) {

		var vm = this;
		
		vm.application = application;
		vm.attempt = attempt;

        // Methods
        vm.close = function() {
        	$mdDialog.cancel();
        };

        vm.print = function() {
        	var table = document.getElementById('printArea').innerHTML;
        	var myWindow = $window.open('', '', 'width=800, height=600');
        	myWindow.document.write(table);
        	myWindow.print();
        };

		vm.init = function() {
			
			var $serie = {
					'color' : '#d62728',
					'values': []
			};
			angular.forEach(vm.attempt.classifications, function(cc, key) {
				var $v = {
						label: $translate.instant('NILA.TABLE.' + cc.table.id),
						value: cc.percent
				};
				$serie.values.push($v);
			});
		
			var $data = [];
			$data.push($serie);
		
			vm.chart.data = $data;
			
		}

		
		
		
        vm.chart = {
                options: {
                    chart: {
                        type              : 'multiBarHorizontalChart',
                        height            : 160,
                        x                 : function (d)
                        {
                            return d.label;
                        },
                        y                 : function (d)
                        {
                            return d.value;
                        },
                        showControls      : false,
                        showValues        : true,
                        transitionDuration: 500,
                        xAxis             : {
                            showMaxMin: false
                        },
                        yAxis             : {
                            axisLabel : '',
                            tickFormat: function (d)
                            {
                                return d3.format(',.2f')(d);
                            }
                        }
                    }
                },
                data   : []
        };
		
		
		
		
        // ////////
        /**
		 * Initialize
		 */
        vm.init();        
        
    }
})();

