(function() {
	'use strict';

	angular
		.module('app.press.verifications')
		.controller('PressVerificationsController', PressVerificationsController);

	/** @ngInject */
	function PressVerificationsController($state, $translate, RestService, PATH, $mdDialog, $document, $mdToast) {

		var vm = this;
		
		vm.items = {};

		vm.dtInstance = {};
		vm.dtOptions = {
			dom : 'rt<"bottom"<"left"<"length"l>><"right"<"info"i><"pagination"p>>>',
			columnDefs : [
					{
						targets   : 0,
						filterable: false,
						sortable  : false,
						responsivePriority : 1
					}, {
						targets   : 1,
						filterable: true,
						sortable  : true,
						responsivePriority : 1,
						width     : '30px'
					}, {
						targets   : 2,
						filterable: true,
						sortable  : true,
						responsivePriority : 1
					}, {
						targets   : 3,
						filterable: true,
						sortable  : true,
						responsivePriority : 1
					}, {
						targets   : 4,
						filterable: true,
						responsivePriority : 2
					}, {
						targets   : 5,
						responsivePriority : 2,
						filterable: false,
						sortable  : true,
						width     : '90px'
					} ],
			initComplete : function() {
				var api = this.api(), searchBox = angular.element('body').find('#search-input');

				// Bind an external input as a table wide search box
				if (searchBox.length > 0) {
					searchBox.on('keyup', function(event) {
						api.search(event.target.value).draw();
					});
				}
			},
			order      : [[ 4, 'asc' ], [ 5, 'desc' ]],
			pagingType : 'numbers',
			lengthMenu : [ 20, 30, 50, 100 ],
			pageLength : 20,
			scrollY    : 'auto',
			responsive : true,
			infoEmpty  : "Sem registros",
			language   : {
				info : "_START_ - _END_ / _TOTAL_"
			}
		};

		// Methods
		vm.detail = function($event, $index, i) {
			
            $mdDialog.show({
                controller         : 'PressVerificationDialogController',
                controllerAs       : 'vm',
                templateUrl        : 'app/main/press/verifications/dialogs/verification-detail/verification-detail-dialog.html',
                parent             : angular.element($document.body),
                targetEvent        : $event,
                clickOutsideToClose: false,
                locals             : {
                    event          : $event,
                    item           : i
                }
            }).then(function(data) {
				vm.items[$index] = data;
			});
			
		};
		vm.remove = function($event, $index, p) {
			
			var confirm = $mdDialog.confirm()
				.title($translate.instant('GLOBAL.DIALOG.REMOVE.TITLE'))
				.textContent($translate.instant('GLOBAL.DIALOG.REMOVE.CONTENT'))
				.ariaLabel($translate.instant('GLOBAL.DIALOG.REMOVE.CONTENT'))
				.targetEvent($event)
				.clickOutsideToClose(true)
				.parent(angular.element(document.body))
				.ok($translate.instant('GLOBAL.DIALOG.REMOVE.CANCEL'))
				.cancel($translate.instant('GLOBAL.DIALOG.REMOVE.REMOVE'));
			
			$mdDialog.show(confirm).then(function() {
				
			}, function() {
	
				RestService.remove({
					url: PATH.rest + 'press/verification/user/document/' + p.id
				}).then(function() {
					vm.items.splice($index, 1);
					
					$mdToast.show(
							$mdToast.simple()
								.textContent($translate.instant('GLOBAL.DIALOG.REMOVE.SUCCESS'))
						    );
				});
				
			});	
			
		};
		vm.init   = function() {

			RestService.get({
				url: PATH.rest + 'press/verification/user/documents'
			}).then(function(response) {
				vm.items = response.data;
			});
			
		};
		
		vm.init();

	}
})();

(function ()
{
    'use strict';

    angular
    	.module('app.press.verifications')
        .controller('PressVerificationDialogController', PressVerificationDialogController);

    /** @ngInject */
    function PressVerificationDialogController(
			$state,
			$translate,
			RestService,
			PATH,
			$mdDialog,
			$document,
			$scope,
			$sce,
			item,
			Valid,
			Converter
		) {

		var url = self.URL || self.webkitURL || self;

		var api = {};
		var vm = this;
		
		vm.item = null;
		vm.blob = null;
		vm.approve = function() {
			RestService.post({
				url: PATH.rest + 'press/verification/user/document/' + item.id + '/approve'
			}).then(function(response) {
	            $mdDialog.hide(response.data);
			}).catch(function(response) {
			});
		}
		vm.reject = function() {
			RestService.post({
				url: PATH.rest + 'press/verification/user/document/' + item.id + '/reject'
			}).then(function(response) {
	            $mdDialog.hide(response.data);
			}).catch(function(response) {
			});
		}
		vm.cancel = function() {
			$mdDialog.cancel();
		}
		vm.init = function() {

			vm.blob = null;
			
			RestService.get({
				url: PATH.rest + 'press/verification/user/document/' + item.id
			}).then(function(response) {
				vm.item = response.data;
				
				if (vm.item.document && vm.item.document.content && vm.item.document.content.bnContent) {
    				var ablob = Converter.base64toBlob(
    						vm.item.document.content.bnContent,
    						vm.item.document.content.mimeType.nmMimeType
    				);
    				vm.blob = $sce.trustAsResourceUrl(        				
    	    			url.createObjectURL(ablob)
        			);
				}
				
				
				
			});
			
		};

		vm.init();

    }
})();

