(function() {
	'use strict';

	angular
		.module('app.press.users')
		.controller('PressUsersController', PressUsersController);

	/** @ngInject */
	function PressUsersController(
			$state, 
			$translate, 
			$mdDialog, 
			$mdToast, 
			$document,
			AuthService,
			PATH,
			RestService) {

		var api = {};
		var vm = this;
		
		vm.profile = AuthService.getProfile();
		
		// Data
//		 0     1     2       3         4   5   6 
//		id,avatar,idcustom,nome, status , last,but
		vm.items = {};

		vm.dtInstance = {};
		vm.dtOptions = {
			dom : 'rt<"bottom"<"left"<"length"l>><"right"<"info"i><"pagination"p>>>',
			columnDefs : [
					{
						targets   : 0,
						filterable: false,
						sortable  : false,
						responsivePriority : 1
					}, {
						targets   : 1,
						filterable: false,
						sortable  : false,
						responsivePriority : 1
					}, {
						targets   : 2,
						filterable: true,
						sortable  : true,
						responsivePriority : 1,
						width     : '30px'
					}, {
						targets   : 3,
						filterable: true,
						sortable  : true,
						responsivePriority : 1
					}, {
						targets   : 4,
						filterable: true,
						sortable  : true,
						responsivePriority : 2
					}, {
						targets   : 5,
						responsivePriority : 2,
						filterable: true,
						sortable  : true,
						width     : '90px'
					}, {
						targets   : 6,
						responsivePriority : 2,
						filterable: true,
						sortable  : true,
						width     : '90px'
					}, {
						targets   : 7,
						responsivePriority : 1,
						filterable: false,
						sortable  : false,
						width     : '120px'
					} ],
			initComplete : function() {
				var api = this.api(), searchBox = angular.element('body').find('#search-input');

				// Bind an external input as a table wide search box
				if (searchBox.length > 0) {
					searchBox.on('keyup', function(event) {
						api.search(event.target.value).draw();
					});
				}
			},
			order      : [[ 6, 'asc' ], [ 3, 'asc' ]],
			pagingType : 'numbers',
			lengthMenu : [ 20, 30, 50, 100 ],
			pageLength : 20,
			scrollY    : 'auto',
			responsive : true,
			infoEmpty  : "Sem registros",
			language   : {
				info : "_START_ - _END_ / _TOTAL_"
			}
		};

		// Methods
		vm.detail = detail;
		vm.add = add;
		vm.remove = remove;
		vm.init = init;
		vm.invite = invite;
		vm.cancel = cancel;
		vm.revoke = revoke;
		
		vm.init();

		// ////////

		/**
		 * Go to product detail
		 * 
		 * @param id
		 */
		function detail(p) {
			$state.go('app.press_users.detail', {
				id : p.id
			});
		};
				
		function add(e) {
			
            var confirm = $mdDialog.show({
                controller         : 'PressUserDialogController',
                controllerAs       : 'vm',
                templateUrl        : 'app/main/press/users/dialogs/person-new-form/person-new-form-dialog.html',
                parent             : angular.element($document.body),
                targetEvent        : e,
                clickOutsideToClose: false,
                locals             : {
                    event          : e
                }
            }).then(function(data) {
            	var p = data;
				if (p.id == vm.profile.id) {
					return;
				}
				vm.items.push(data);
			});
		};
		
		function init() {

			RestService.get({
				url: PATH.rest + 'press/users'
			}).then(function(response) {
				vm.items = response.data;
			});

		};

		function remove($event, $index, p) {

			var confirm = $mdDialog.confirm()
				.title($translate.instant('GLOBAL.DIALOG.REMOVE.TITLE'))
				.textContent($translate.instant('GLOBAL.DIALOG.REMOVE.CONTENT'))
				.ariaLabel($translate.instant('GLOBAL.DIALOG.REMOVE.CONTENT'))
				.targetEvent($event)
				.clickOutsideToClose(true)
				.parent(angular.element(document.body))
				.ok($translate.instant('GLOBAL.DIALOG.REMOVE.CANCEL'))
				.cancel($translate.instant('GLOBAL.DIALOG.REMOVE.REMOVE'));
			
			$mdDialog.show(confirm).then(function() {
				
			}, function() {

				RestService.remove({
					url: PATH.rest + 'press/user/' + p.id
				}).then(function() {
					vm.items.splice($index, 1);
					
					$mdToast.show(
							$mdToast.simple()
								.textContent($translate.instant('GLOBAL.DIALOG.REMOVE.SUCCESS'))
						    );
				});
				
			});	
			
		}
		
		function invite($event, $index, p) { changeState($event, $index, p, 'INVITED', 'INVITE'); }
		function cancel($event, $index, p) { changeState($event, $index, p, 'CANCELED', 'CANCEL'); }
		function revoke($event, $index, p) { changeState($event, $index, p, 'REVOKED', 'REVOKE'); }
		
		function changeState($event, $index, p, state, action) {
			var confirm = $mdDialog.confirm()
			.title($translate.instant('GLOBAL.' + action))
			.textContent(vm.items[$index].user.nmUser)
			.ariaLabel(vm.items[$index].user.nmUser)
			.targetEvent($event)
			.clickOutsideToClose(true)
			.parent(angular.element(document.body))
			.ok($translate.instant('GLOBAL.' + action))
			.cancel($translate.instant('GLOBAL.CLOSE'));
		
			$mdDialog.show(confirm).then(function() {

//				RestService.create(PATH.rest + 'user/:id/state/:state', {id: p.id, state: state})
//				.post().$promise.then(function(profile) {
//
//					vm.items[$index] = profile;
//					$mdToast.show(
//							$mdToast.simple()
//								.textContent($translate.instant('GLOBAL.SUCCESS'))
//						    );
//				});

				RestService.post({
					url: PATH.rest + 'press/user/' + p.id + '/state/' + state
				}).then(function(response) {

					var p = response.data;
					vm.items[$index] = p;
					$mdToast.show(
							$mdToast.simple()
								.textContent($translate.instant('GLOBAL.SUCCESS'))
						    );
				});
				
			}, function() {
				
			});	
			
		}
		
	}
})();

(function() {
	'use strict';

	angular
		.module('app.press.users')
		.controller('PressUserController', PressUserController);

	/** @ngInject */
	function PressUserController($document, $state, RestService, PATH) {
		
		var api = {};
		var vm = this;
		
		vm.disabled = true;

		// Data
		vm.taToolbar = [
				[ 'h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'p', 'pre', 'quote',
						'bold', 'italics', 'underline', 'strikeThrough', 'ul',
						'ol', 'redo', 'undo', 'clear' ],
				[ 'justifyLeft', 'justifyCenter', 'justifyRight',
						'justifyFull', 'indent', 'outdent', 'html',
						'insertImage', 'insertLink', 'insertVideo',
						'wordcount', 'charcount' ] ];

		vm.categoriesSelectFilter = '';
		vm.ngFlowOptions = {
		// You can configure the ngFlow from here
		/*
		 * target : 'api/media/image', chunkSize : 15 * 1024 * 1024,
		 * maxChunkRetries : 1, simultaneousUploads : 1, testChunks : false,
		 * progressCallbacksInterval: 1000
		 */
		};
		vm.ngFlow = {
			// ng-flow will be injected into here through its directive
			flow : {}
		};
		vm.dropping = false;
		
		vm.profile = {};

		// Methods
		vm.list = list;
		vm.onCategoriesSelectorOpen = onCategoriesSelectorOpen;
		vm.onCategoriesSelectorClose = onCategoriesSelectorClose;
		vm.fileAdded = fileAdded;
		vm.upload = upload;
		vm.fileSuccess = fileSuccess;
		vm.save = save;
		vm.edit = edit;
		vm.init = init;

		// ////////

		vm.init();

		/**
		 * Initialize
		 */
		function init() {
			// Select the correct student from the data.
			// This is an unnecessary step for a real world app
			// because normally, you would request the product
			// with its id and you would get only one product.
			// For demo purposes, we are grabbing the entire
			// json file which have more than one product details
			// and then hand picking the requested product from
			// it.
			var id = $state.params.id;

        	RestService.create(PATH.rest + 'press/user/:id', {id: '@id'})
        		.get({'id': id}).$promise.then(function(profile) {
        			
        		vm.profile = profile;
        		vm.profile.user.dateBirthday = new Date(vm.profile.user.dtBirthday);
        		vm.profile.user.dateDeathday = new Date(vm.profile.user.dtDeathday);
        		
        	});
			// END - Select the correct student from the data
		}
		
		/**
		 * Go to students page
		 */
		function list() {
			$state.go('app.press_users');
		}

		/**
		 * On categories selector open
		 */
		function onCategoriesSelectorOpen() {
			// The md-select directive eats keydown events for some quick select
			// logic. Since we have a search input here, we don't need that
			// logic.
			$document.find('md-select-header input[type="search"]').on(
					'keydown', function(e) {
						e.stopPropagation();
					});
		}

		/**
		 * On categories selector close
		 */
		function onCategoriesSelectorClose() {
			// Clear the filter
			vm.categoriesSelectFilter = '';

			// Unbind the input event
			$document.find('md-select-header input[type="search"]').unbind(
					'keydown');
		}

		/**
		 * File added callback Triggers when files added to the uploader
		 * 
		 * @param file
		 */
		function fileAdded(file) {
			// Prepare the temp file data for media list
			var uploadingFile = {
				id : file.uniqueIdentifier,
				file : file,
				type : 'uploading'
			};

			// Append it to the media list
			vm.product.images.unshift(uploadingFile);
		}

		/**
		 * Upload Automatically triggers when files added to the uploader
		 */
		function upload() {
			// Set headers
			vm.ngFlow.flow.opts.headers = {
				'X-Requested-With' : 'XMLHttpRequest',
			// 'X-XSRF-TOKEN' : $cookies.get('XSRF-TOKEN')
			};

			vm.ngFlow.flow.upload();
		}

		/**
		 * File upload success callback Triggers when single upload completed
		 * 
		 * @param file
		 * @param message
		 */
		function fileSuccess(file, message) {
			// Iterate through the media list, find the one we
			// are added as a temp and replace its data
			// Normally you would parse the message and extract
			// the uploaded file data from it
			angular.forEach(vm.product.images, function(media, index) {
				if (media.id === file.uniqueIdentifier) {
					// Normally you would update the media item
					// from database but we are cheating here!
					var fileReader = new FileReader();
					fileReader.readAsDataURL(media.file.file);
					fileReader.onload = function(event) {
						media.url = event.target.result;
					};

					// Update the image type so the overlay can go away
					media.type = 'image';
				}
			});
		}

		function save() {
			
			vm.profile.user.dtBirthday = vm.profile.user.dateBirthday.toISOString();
			
			var s = JSON.parse(JSON.stringify(vm.profile));
			
			delete s.user['dateBirthday'];
			delete s.user['dateDeathday'];
			
//			delete s['auths'];
//			delete s['profiles'];
//			delete s['update'];
//			delete s['userRelations'];
//			delete s['documents'];

			vm.disabled = true;

			RestService.post({
				url: PATH.rest + 'student',
				data: s
			}, function(response) {
				
			}, function(response) {
				
			});
		}
		
		function edit() {
			vm.disabled = false;
		}
		
	}
	
})();

(function ()
{
    'use strict';

    angular
    	.module('app.press.users')
        .controller('PressUserDialogController', PressUserDialogController);

    /** @ngInject */
    function PressUserDialogController($translate, $state, $mdDialog, RestService, PATH, $scope, Valid)
    {
        var vm = this;
        
        $scope.data = {
       		profiles: [
               	{
               		id  : 10000,
               		name: $translate.instant('ROLE.10000')
               	}
            ]
        };
        
        $scope.principalValidation = (function() {
			return {
		    	test: function(value) {
		    		vm.person.nmUser = null;
					vm.person.disabledName = true;
		    		if (!Valid.email(value)) return false;
		    		
					RestService.get({
						url: PATH.rest + 'user/doc/EMAIL/' + value
					}).then(function(response) {
						var user = response.data;
						var hasUser = user != null && user != "";
						vm.person.disabledName = hasUser;
						vm.person.nmUser = hasUser ? user.nmUser : null;
					}, function(response) {
					});
					return true;
		        }
		    };
		})();

        // Data
        vm.person = {
        	principal: null,
        	nmUser   : null,
        	profile  : $scope.data.profiles[0], 
        	disabledName: true
        };

        // Methods
        vm.cancel = cancel;
        vm.next = next;
        
        // ////////

        /**
		 * Next
		 */
        function next()
        {
        	var profile = {
        		user: {
        			nmUser: vm.person.nmUser,
        			documents: [
       					{
       						type: {
       							id: 3,
       						},
       						nmPrincipal: vm.person.principal
       					}
       				]
        		},
        		profile: {
        			id: vm.person.profile.id
        		}
        	};
			
			RestService.post({
				url: PATH.rest + 'press/user/admin',
				data: profile
			}).then(function(response) {
	            $mdDialog.hide(response.data);
			}).catch(function(response) {
			});

        }

        /**
		 * Cancel
		 */
        function cancel()
        {
            $mdDialog.cancel();
        }
        
    }
})();

