(function() {
	'use strict';

	angular
		.module('app.press.resellers')
		.controller('PressResellerController', PressResellerController);

	/** @ngInject */
	function PressResellerController($document, $state, RestService, PATH) {
		
		var api = {};
		var vm = this;
		
		vm.reseller = null;
		vm.list = function() {
			$state.go('app.press_resellers');
		}

		vm.users = {};
		vm.users.dtInstance = {};
		vm.users.dtOptions = {
			dom : 'rt<"bottom"<"left"<"length"l>><"right"<"info"i><"pagination"p>>>',
			columnDefs : [
					{
						// Target the image column
						// photo
						targets : 0,
						filterable : false,
						sortable : false,
						width : '80px',
						responsivePriority : 1
					}, {
						// Name
						targets : 1,
						responsivePriority : 1
					}, {
						// Profile
						targets : 2,
						responsivePriority : 1
					}, {
						// Update
						targets : 3,
						responsivePriority : 2,
						filterable : true,
						sortable : true
					}, {
						// State
						targets : 4,
						width : '80px',
						responsivePriority : 2,
						filterable : true,
						sortable : true
					}, {
						// Target the actions column
						targets : 5,
						width : '120px',
						responsivePriority : 1,
						filterable : false,
						sortable : false
					}, {
						// Target the actions column
						targets : 6,
						width : '40px',
						responsivePriority : 2,
						filterable : false,
						sortable : false
					} ],
			initComplete : function() {
				var api = this.api(), searchBox = angular.element('body').find(
						'#persons-search');

				// Bind an external input as a table wide search box
				if (searchBox.length > 0) {
					searchBox.on('keyup', function(event) {
						api.search(event.target.value).draw();
					});
				}
			},
			pagingType : 'numbers',
			lengthMenu : [ 10, 20, 30, 50, 100 ],
			pageLength : 20,
			scrollY : 'auto',
			responsive : true,
			language : {
				info : "_START_ - _END_ / _TOTAL_"
			}
		};
		
		
		
		vm.init = function() {
			var id = $state.params.id;
			RestService.get({
				url: PATH.rest + 'press/reseller/' + id,
			}).then(function(response) {
				vm.reseller = response.data;
				vm.getUsers();
			});
		}

		vm.getUsers = function() {
			RestService.get({
				url: PATH.rest + 'press/reseller/' + vm.reseller.id + '/users',
			}).then(function(response) {
				vm.reseller.to.users = response.data;
			});
		}

		vm.init();
		
		return;
		
		vm.disabled = true;

		// Data
		vm.taToolbar = [
				[ 'h1', 'h2', 'h3', 'h4', 'h5', 'h6', 'p', 'pre', 'quote',
						'bold', 'italics', 'underline', 'strikeThrough', 'ul',
						'ol', 'redo', 'undo', 'clear' ],
				[ 'justifyLeft', 'justifyCenter', 'justifyRight',
						'justifyFull', 'indent', 'outdent', 'html',
						'insertImage', 'insertLink', 'insertVideo',
						'wordcount', 'charcount' ] ];

		vm.categoriesSelectFilter = '';
		vm.ngFlowOptions = {
		// You can configure the ngFlow from here
		/*
		 * target : 'api/media/image', chunkSize : 15 * 1024 * 1024,
		 * maxChunkRetries : 1, simultaneousUploads : 1, testChunks : false,
		 * progressCallbacksInterval: 1000
		 */
		};
		vm.ngFlow = {
			// ng-flow will be injected into here through its directive
			flow : {}
		};
		vm.dropping = false;
		
		vm.profile = {};

		// Methods
		vm.gotoPersons = gotoPersons;
		vm.onCategoriesSelectorOpen = onCategoriesSelectorOpen;
		vm.onCategoriesSelectorClose = onCategoriesSelectorClose;
		vm.fileAdded = fileAdded;
		vm.upload = upload;
		vm.fileSuccess = fileSuccess;
		vm.save = save;
		vm.edit = edit;
		vm.init = init;

		// ////////

		vm.init();

		/**
		 * Initialize
		 */
		function init() {
			// Select the correct student from the data.
			// This is an unnecessary step for a real world app
			// because normally, you would request the product
			// with its id and you would get only one product.
			// For demo purposes, we are grabbing the entire
			// json file which have more than one product details
			// and then hand picking the requested product from
			// it.
			var id = $state.params.id;

        	RestService.create(PATH.rest + 'company/user/:id', {id: '@id'})
        		.get({'id': id}).$promise.then(function(profile) {
        			
        		vm.profile = profile;
        		vm.profile.user.dateBirthday = new Date(vm.profile.user.dtBirthday);
        		vm.profile.user.dateDeathday = new Date(vm.profile.user.dtDeathday);
        		
        	});
			// END - Select the correct student from the data
		}
		
		/**
		 * Go to students page
		 */
		function gotoPersons() {
			$state.go('app.persons.students');
		}

		/**
		 * On categories selector open
		 */
		function onCategoriesSelectorOpen() {
			// The md-select directive eats keydown events for some quick select
			// logic. Since we have a search input here, we don't need that
			// logic.
			$document.find('md-select-header input[type="search"]').on(
					'keydown', function(e) {
						e.stopPropagation();
					});
		}

		/**
		 * On categories selector close
		 */
		function onCategoriesSelectorClose() {
			// Clear the filter
			vm.categoriesSelectFilter = '';

			// Unbind the input event
			$document.find('md-select-header input[type="search"]').unbind(
					'keydown');
		}

		/**
		 * File added callback Triggers when files added to the uploader
		 * 
		 * @param file
		 */
		function fileAdded(file) {
			// Prepare the temp file data for media list
			var uploadingFile = {
				id : file.uniqueIdentifier,
				file : file,
				type : 'uploading'
			};

			// Append it to the media list
			vm.product.images.unshift(uploadingFile);
		}

		/**
		 * Upload Automatically triggers when files added to the uploader
		 */
		function upload() {
			// Set headers
			vm.ngFlow.flow.opts.headers = {
				'X-Requested-With' : 'XMLHttpRequest',
			// 'X-XSRF-TOKEN' : $cookies.get('XSRF-TOKEN')
			};

			vm.ngFlow.flow.upload();
		}

		/**
		 * File upload success callback Triggers when single upload completed
		 * 
		 * @param file
		 * @param message
		 */
		function fileSuccess(file, message) {
			// Iterate through the media list, find the one we
			// are added as a temp and replace its data
			// Normally you would parse the message and extract
			// the uploaded file data from it
			angular.forEach(vm.product.images, function(media, index) {
				if (media.id === file.uniqueIdentifier) {
					// Normally you would update the media item
					// from database but we are cheating here!
					var fileReader = new FileReader();
					fileReader.readAsDataURL(media.file.file);
					fileReader.onload = function(event) {
						media.url = event.target.result;
					};

					// Update the image type so the overlay can go away
					media.type = 'image';
				}
			});
		}

		function save() {
			
			vm.profile.user.dtBirthday = vm.profile.user.dateBirthday.toISOString();
			
			var s = JSON.parse(JSON.stringify(vm.profile));
			
			delete s.user['dateBirthday'];
			delete s.user['dateDeathday'];
			
//			delete s['auths'];
//			delete s['profiles'];
//			delete s['update'];
//			delete s['userRelations'];
//			delete s['documents'];

			vm.disabled = true;

			RestService.post({
				url: PATH.rest + 'student',
				data: s
			}, function(response) {
				
			}, function(response) {
				
			});
		}
		
		function edit() {
			vm.disabled = false;
		}
		
	}
	
})();

(function ()
{
    'use strict';

    angular
    	.module('app.press.resellers')
        .controller('PressResellerDialogController', PressResellerDialogController);

    /** @ngInject */
    function PressResellerDialogController(
			$state,
			$translate,
			RestService,
			PATH,
			$mdDialog,
			$document,
			$scope,
			Valid
		) {

		var api = {};
		var vm = this;
		
		vm.admin = {
			id   : null,
			email: null,
			name : null,
			disabledName: true
		};
		
		vm.corporate = {
			id  : null,
			cnpj: null,
			name: null,
			disabledName: true
		};
		
		$scope.cnpjValidation = (function() {
			return {
		    	test: function(value) {
		    		vm.corporate.name = null;
		    		vm.corporate.disabledName = true;
		        	if (!Valid.cnpj(value)) return false;
		        	
		        	var $cnpj = value.replace(replaceAll("[^\\d]", ""));
		        	
					RestService.get({
						url: PATH.rest + 'corporate/doc/CNPJ/' + $cnpj,
					}).then(function(response) {
						var corporate = response.data;
						var hasUser = corporate != null && corporate != "";
						vm.corporate.disabledName = hasUser;
						vm.corporate.id   = hasUser ? corporate.id          : null;
						vm.corporate.name = hasUser ? corporate.nmCorporate : null;
					}, function(response) {
					});
					return true;
		        }
		    };
		})();

        $scope.principalValidation = (function() {
			return {
		    	test: function(value) {
		    		vm.admin.name = null;
					vm.admin.disabledName = true;
		    		if (!Valid.email(value)) return false;
		    		
					RestService.get({
						url: PATH.rest + 'user/doc/EMAIL/' + value
					}).then(function(response) {
						var user = response.data;
						var hasUser = user != null && user != "";
						vm.admin.disabledName = hasUser;
						vm.admin.id   = hasUser ? user.id     : null;
						vm.admin.name = hasUser ? user.nmUser : null;
					}, function(response) {
					});
					return true;
		        }
		    };
		})();

        // Methods
        vm.cancel = cancel;
        vm.submit = submit;
        

        init();

        // ////////

        /**
		 * Initialize
		 */
        function init()
        {

        }

        /**
		 * Next
		 */
        function submit()
        {
        	var usercorporate = {
        		corporate:
        		{
        			id  : vm.corporate.id,
        			nmCorporate: vm.corporate.name,
       				documents: [
       					{
       						type:
       						{
       							id: 1001
       						},
       						nmPrincipal: vm.corporate.cnpj
       					}
       				]
       			},
       			profile: 10000,
       			user:
       			{
       				id  : vm.admin.id,
       				nmUser: vm.admin.name,
       				documents: [
       					{
       						type:
       						{
       							id: 3
       						},
       						nmPrincipal: vm.admin.email
       					}
       				]
       			}
        	};
			
			RestService.post({
				url: PATH.rest + 'press/reseller',
				data: usercorporate
			}).then(function(response) {
	            $mdDialog.hide(response.data);
			}).catch(function(response) {
			});

        }

        /**
		 * Cancel
		 */
        function cancel()
        {
            $mdDialog.cancel();
        }
        
    }
})();

