(function() {
	'use strict';

	angular
		.module('app.clinic.patients')
		.controller('ClinicPatientController', ClinicPatientController);

	/** @ngInject */
	function ClinicPatientController($document, $state, RestService, PATH, $mdToast, $translate, Valid) {
		
		var api = {};
		var vm = this;
		
		vm.isNew = false;
		vm.patient = null;
		vm.list = function() {
			$state.go('app.clinic_patients');
		}
		
		vm.refresh = function(patient) {
			
			var neededDocuments = { '1002': 'CPF', '1003': 'RG' };
			
			if (!patient.documents) {
				patient.documents = [];
			}
			for (var i = 0; i < patient.documents.length; i++) {
				var id = patient.documents[i].type.id;
				if (neededDocuments[id]) {
					delete neededDocuments[id];
				};
			}
			
			for (var k in neededDocuments) {
				var doc = {
					type: {
						id: k
					}
				};
				patient.documents.push(doc);
			}
			vm.patient = patient;
		}
		
        vm.init = function() {
    		var id = $state.params.id;
    		RestService.get({
    			url: PATH.rest + 'clinic/patient/' + id,
    		}).then(function(response) {
    			vm.refresh(response.data);
    		});

        }
        
        vm.save = function() {
        	
    		RestService.put({
    			url: PATH.rest + 'clinic/patient',
    			data: vm.patient
    		}).then(function(response) {
    			vm.refresh(response.data);
				$mdToast.show(
						$mdToast.simple()
							.textContent($translate.instant('GLOBAL.DIALOG.SAVE.SUCCESS'))
					    );
    		});
        	
        }

        vm.init();

	}
	
})();

