(function ()
{
    'use strict';

    angular
    	.module('app.pages.dialogs.postalcode-search', [])
        .controller('PostalCodeSearchDialogController', PostalCodeSearchDialogController);

    /** @ngInject */
    function PostalCodeSearchDialogController($translate, $state, $mdDialog, $scope,
    		PATH, RestService, PostalCodeService)
    {
        var vm = this;
        
        vm.address = null;
        vm.publicplace = null;
        vm.country = null;
        vm.state   = null;
        vm.city    = null;
        
        vm.addresses = [];
        vm.countries = [];
        vm.states = [];
        vm.cities = [];
        
        // Methods
        vm.cancel = cancel;
        vm.next = next;
        vm.init = init;
        vm.changeCountry = changeCountry;
        vm.changeState = changeState;
        vm.changeCity = changeCity;
        vm.changePublicPlace = changePublicPlace;
        
        vm.init();
        
        
        function init()
        {
            vm.countries = [];
            vm.states = [];
            vm.cities = [];
            vm.addresses = [];

            vm.country = null;
            vm.state   = null;
            vm.city    = null;
            vm.address = null;
            vm.publicplace = null;

			RestService.get({ url: PATH.rest + 'geo/countries' })
			.then(function(response) {
				vm.countries = response.data;
				vm.country = vm.countries[0];
				changeCountry();
			});
        }
        
        function changeCountry()
        {
            vm.states    = [];
            vm.cities    = [];
            vm.addresses = [];
            
            vm.state   = null;
            vm.city    = null;
            vm.address = null;
            vm.publicplace = null;
            
			RestService.get({ url: PATH.rest + 'geo/country/' + vm.country.id + '/states' })
			.then(function(response) {
				vm.states = response.data;
			});
        }

        function changeState()
        {
            vm.cities    = [];
            vm.addresses = [];
            
            vm.city    = null;
            vm.address = null;
            vm.publicplace = null;
            
			RestService.get({ url: PATH.rest + 'geo/state/' + vm.state.id + '/cities' })
			.then(function(response) {
				vm.cities = response.data;
			});
        }

        function changeCity()
        {
            vm.addresses = [];
            
            vm.address = null;
            vm.publicplace = null;
        }

        function changePublicPlace()
        {
            vm.addresses = [];
            
            vm.address = null;
            
            if (vm.publicplace && vm.publicplace.length > 2) {
				PostalCodeService.lookup(vm.state.nmAbbrev, vm.city.nmCity, vm.publicplace)
					.then(function(response) {
				        vm.addresses = response.data;
					}).catch(function(response) {
					});
            }
        }

        // ////////

        /**
		 * Next
		 */
        function next()
        {
        	var $addr = vm.address;
        	if (!$addr) {
        		return;
        	}
        	$mdDialog.hide($addr);
        }

        /**
		 * Cancel
		 */
        function cancel()
        {
            $mdDialog.cancel();
        }
        
    }
})();

(function ()
{
    'use strict';

    angular
    	.module('app.pages.dialogs.photo-upload', [])
        .controller('PhotoUploadDialogController', PhotoUploadDialogController);

    /** @ngInject */
    function PhotoUploadDialogController($translate, $state, $mdDialog, $scope,
    		PATH, RestService, PostalCodeService)
    {
        var vm = this;
        
        vm.address = null;
        vm.publicplace = null;
        vm.country = null;
        vm.state   = null;
        vm.city    = null;
        
        vm.addresses = [];
        vm.countries = [];
        vm.states = [];
        vm.cities = [];
        
        // Methods
        vm.cancel = cancel;
        vm.next = next;
        vm.init = init;
        vm.changeCountry = changeCountry;
        vm.changeState = changeState;
        vm.changeCity = changeCity;
        vm.changePublicPlace = changePublicPlace;
        
        vm.init();
        
        
        function init()
        {
            vm.countries = [];
            vm.states = [];
            vm.cities = [];
            vm.addresses = [];

            vm.country = null;
            vm.state   = null;
            vm.city    = null;
            vm.address = null;
            vm.publicplace = null;

			RestService.get({ url: PATH.rest + 'geo/countries' })
			.then(function(response) {
				vm.countries = response.data;
				vm.country = vm.countries[0];
				changeCountry();
			});
        }
        
        function changeCountry()
        {
            vm.states    = [];
            vm.cities    = [];
            vm.addresses = [];
            
            vm.state   = null;
            vm.city    = null;
            vm.address = null;
            vm.publicplace = null;
            
			RestService.get({ url: PATH.rest + 'geo/country/' + vm.country.id + '/states' })
			.then(function(response) {
				vm.states = response.data;
			});
        }

        function changeState()
        {
            vm.cities    = [];
            vm.addresses = [];
            
            vm.city    = null;
            vm.address = null;
            vm.publicplace = null;
            
			RestService.get({ url: PATH.rest + 'geo/state/' + vm.state.id + '/cities' })
			.then(function(response) {
				vm.cities = response.data;
			});
        }

        function changeCity()
        {
            vm.addresses = [];
            
            vm.address = null;
            vm.publicplace = null;
        }

        function changePublicPlace()
        {
            vm.addresses = [];
            
            vm.address = null;
            
            if (vm.publicplace && vm.publicplace.length > 2) {
				PostalCodeService.lookup(vm.state.nmAbbrev, vm.city.nmCity, vm.publicplace)
					.then(function(response) {
				        vm.addresses = response.data;
					}).catch(function(response) {
					});
            }
        }

        // ////////

        /**
		 * Next
		 */
        function next()
        {
        	var $addr = vm.address;
        	if (!$addr) {
        		return;
        	}
        	$mdDialog.hide($addr);
        }

        /**
		 * Cancel
		 */
        function cancel()
        {
            $mdDialog.cancel();
        }
        
    }
})();

(function ()
{
    'use strict';

    angular
    	.module('app.pages.dialogs.pendency-inform', [])
        .controller('PendencyInformDialogController', PendencyInformDialogController)
		.directive('uniqueCpf', function(RestService, Valid, PATH, AUTH_EVENTS) {
			var toId;
			return {
				restrict: 'A',
				require: 'ngModel',
				link: function (scope, elem, attrs, mCtrl) {
					function uniqueCpf(value) {
						// if there was a previous attempt, stop it.
						if(toId) clearTimeout(toId);
	
						// start a new attempt with a delay to keep it from
						// getting too "chatty".
						toId = setTimeout(function(){
							
							if (!Valid.cpf(value)) {
								mCtrl.$setValidity('uniqueCpf', Boolean(true));
								return;
							}
							
							// call to some API that returns { isValid: true } or { isValid: false }
							RestService.get({
								url: PATH.rest + 'user/doc/CPF/' + value
							}).then(function(response) {
								mCtrl.$setValidity('uniqueCpf', Boolean(!response.data));
							}, function(error) { });
						
						}, 200);
						return value;
					}
					mCtrl.$setValidity('uniqueCpf', Boolean(false));
					mCtrl.$parsers.push(uniqueCpf);
				}
			}
		});
        

    /** @ngInject */
    function PendencyInformDialogController($translate, $state, $mdDialog, $scope, $window,
//    		Documents,
    		pendency, corporate,
    		PATH, RestService, PostalCodeService, Valid)
    {
    	
        var vm = this;
        
        vm.pendency = pendency;
        vm.idType = vm.pendency.type.id;
        vm.hasContent = vm.idType === '1100';
        
        vm.principal = null;
        vm.content = null;
        
        vm.valited = false;
        vm.error = null;
        
        $scope.cpfValidation = (function() {
			return {
		    	test: function(value) {
		    		return Valid.cpf(value);
		        }
		    };
		})();      

        vm.ngFlowOptions = {
			// You can configure the ngFlow from here
			/*target                   : 'api/media/image',
			chunkSize                : 15 * 1024 * 1024,
			maxChunkRetries          : 1,
			simultaneousUploads      : 1,
			testChunks               : false,
			progressCallbacksInterval: 1000*/
        };
        vm.ngFlow = {
        	// ng-flow will be injected into here through its directive
        	flow: {}
        };

        vm.cancel = function() {
            $mdDialog.cancel();
        }
        
        vm.submit = function($event, $form) {
            vm.error = null;
            
            var payload = new FormData();
            payload.append('corporate', PATH.idCorporate);
            payload.append('principal', vm.principal);
            
            if (vm.hasContent && $scope.content) {
                payload.append('content', $scope.content);
            }
            
			RestService.post({
				url             : PATH.rest + 'user/document/' + vm.idType + '/multipart',
				headers         : { 'Content-Type': undefined },
				contentType     : false,
				transformRequest: function (data) {
					return payload;
				}
			}).then(function(response) {
	            $mdDialog.hide();
			}, function(error) {
				vm.error = {
					code: error.status,
					text: error.statusText,
					message: $translate.instant('GLOBAL.DOCUMENT.ERRORS.SERVER.' + error.status)
				}
			});
        };
        
    }
})();

