(function ()
{
    'use strict';

    angular
        .module('app.pages.auth.register')
        .controller('RegisterController', RegisterController)
		.directive('uniqueEmail', function($http, PATH, AUTH_EVENTS) {
			var toId;
			var PATH = PATH.base + "auth/";
			return {
				restrict: 'A',
				require: 'ngModel',
				link: function (scope, elem, attrs, mCtrl) {
					function uniqueEmail(value) {
						// if there was a previous attempt, stop it.
						if(toId) clearTimeout(toId);
	
						// start a new attempt with a delay to keep it from
						// getting too "chatty".
						toId = setTimeout(function(){
							// call to some API that returns { isValid: true } or { isValid: false }
							$http.get(PATH + "" + value).success(function(data) {
								//set the validity of the field
								mCtrl.$setValidity('uniqueEmail', !value || Boolean(data));
							});
						}, 200);
						return value;
					}
					mCtrl.$parsers.push(uniqueEmail);
				}
			}
		});
        

    /** @ngInject */
    function RegisterController(
			$state, $rootScope, $anchorScroll, $location, $mdMedia, $scope,
			$mdDialog, $translate, RestService, AuthService,
			$document, PATH, AUTH_EVENTS)
    {
		// Data
		var vm = this;

		// Methods

		// ////////

		vm.register = {
			username       : '',
			email          : '',
			password       : '',
			passwordConfirm: '',
			cpf            : '',
			agree          : false,
			error          : null
		};
		
		vm.post = post;
		
		vm.showTerm = function(ev) {
        	$mdDialog.show({
        		controller         : 'TermDialogController',
                controllerAs       : 'vm',
        		templateUrl        : 'app/main/pages/dialogs/term/term-dialog.html',
                parent             : angular.element($document.body),
        		targetEvent        : ev,
        		clickOutsideToClose: true,
        		escapeToClose      : true,
        		fullscreen         : ($mdMedia('sm') || $mdMedia('xs')) && $scope.customFullscreen,
                locals             : {
                    event          : ev
                }
        	})
        	.then(function() {
        	}, function() {
        	});
		}

		function post($event) {
			
			vm.register.error = null;
			
			RestService.post({
					url : PATH.rest + "registration",
					data: {
						nmUser      : vm.register.username,
						documentType: {
							id: 3
						},
						nmPrincipal : vm.register.email,
						nmCredential: vm.register.password
					}					
				}).then(
					function(response) {
					
						$mdDialog
							.show(
								$mdDialog.alert()
									.parent(angular.element(document.querySelector('#register-form-wrapper')))
									.clickOutsideToClose(true)
									.parent(angular.element(document.body))
									.title($translate.instant('REGISTER.DIALOGS.DONE.TITLE'))
									.textContent(String.format($translate.instant('REGISTER.DIALOGS.DONE.CONTENT'), vm.register.username, vm.register.email))
									.ariaLabel(String.format($translate.instant('REGISTER.DIALOGS.DONE.CONTENT'), vm.register.username, vm.register.email))
									.ok($translate.instant('REGISTER.DIALOGS.DONE.CLOSE'))
									.targetEvent($event)
						);
						$state.go('app.pages_auth_login');
						
					},
					function(error) {
						
						var code = (error.status == 500 && error.data && error.data.id) ? error.data.id : error.status;
						
						vm.register.password = null;
						vm.register.passwordConfirm = null;
						vm.register.error = {
							code: error.status,
							text: error.statusText,
							message: String.format($translate.instant('REGISTER.ERRORS.SERVER.' + code), vm.register.email)
						}
					}
				);
		};
    }
})();
