(function ()
{
    'use strict';

    angular
        .module('app.pages.auth.login')
        .controller('LoginController', LoginController);

    /** @ngInject */
    function LoginController(AuthService, AUTH_EVENTS, $translate, $rootScope, $scope, $state, Valid, Mask)
    {
        // Data
		var vm = this;

		// Methods

		// ////////

		vm.login = {
				principal: {
					value : null,
					change: function() {
						var v = this.value;
			    		if (v && v.match(/^\d/)) {
			    			this.value = Mask.cpf(v);
			    		}
					},
					type  : function() {
						return Valid.email(this.value) ? 'EMAIL' :
							   Valid.cpf(this.value)   ? 'CPF'   :
							   null;
					},
					isValid: (function() {
						return {
					    	test: function(value) {
					    		return Valid.email(value) || Valid.cpf(value);
					        }
					    };
					})()
				},
				password : {
					value: null
				},
				remember : false,
				error    : null,
		};

//		vm.login.principal.value = "humberto.sandmann@gmail.com";
//		vm.login.password.value = "123";
		
//		$scope.$watch('vm.login.principal.value', function change(newV, oldV) {
//			if (!newV) return;
//			var i = newV;
//			if (i.length > 0 && i.match(/^\d/)) {
//				vm.login.principal.value = Mask.cpf(i);
//			}	    	
//		});

		vm.apply = function($event) {
			
		    vm.login.error = null;
		    
		    var $type = vm.login.principal.type();
		    
		    if ($type == null) {
		    	console.error("Identification is not valid");
		    	return;
		    }
		    
		    var $principal = vm.login.principal.value;
		    var $password  = vm.login.password.value;

			AuthService.login($type, $principal, $password,
				function(response) {
					
					var data = response.data;
					$rootScope.$broadcast(AUTH_EVENTS.loginSuccess);
					$state.go( 'app.setup' );
					
				}, function(error) {
					
					var code = (error.status == 500 && error.data && error.data.id) ? error.data.id : error.status;
					
					$rootScope.$broadcast(AUTH_EVENTS.loginFailed);
					vm.login.password = null;
					vm.login.error = {
						code: error.status,
						text: error.statusText,
						message: $translate.instant('LOGIN.ERRORS.SERVER.' + code)
					}
					
				});
			
		};
        //////////
    }
})();
