(function ()
{
    'use strict';

    angular
        .module('app.pages.auth.forgot-password')
        .controller('ForgotPasswordController', ForgotPasswordController);

    /** @ngInject */
    function ForgotPasswordController(Valid, RestService, PATH, $mdDialog, $translate, $state)
    {
    	
        // Data
		var vm = this;
    	
        // Data
    	vm.form = {
    		email: null
    	};
    	
    	vm.request = request;
    	
    	function request($event) {
    		
		    vm.form.error = null;
		    
		    var $type = Valid.email(vm.form.principal) ? 'EMAIL'
		    		  : Valid.cpf(vm.form.principal)   ? 'CPF'
		    		  : null;
		    
		    if ($type == null) {
		    	console.erro("Identification is not valid");
		    	return;
		    }
		    
		    var $principal = vm.form.principal;
		    var $password  = vm.form.password;
		    
			RestService.get({
				url : PATH.rest + "auth/reset/" + $type + "/" + $principal
			}).then(
				function(response) {
				
					$mdDialog
						.show(
							$mdDialog.alert()
								.parent(angular.element(document.querySelector('#forgot-password-form-wrapper')))
								.clickOutsideToClose(true)
								.parent(angular.element(document.body))
								.title($translate.instant('FORGOTPASSWORD.DIALOGS.DONE.TITLE'))
								.textContent(String.format($translate.instant('FORGOTPASSWORD.DIALOGS.DONE.CONTENT')))
								.ariaLabel(String.format($translate.instant('FORGOTPASSWORD.DIALOGS.DONE.CONTENT')))
								.ok($translate.instant('FORGOTPASSWORD.DIALOGS.DONE.CLOSE'))
								.targetEvent($event)
					);
					$state.go('app.pages_auth_login');
					
				},
				function(error) {

					var code = (error.status == 500 && error.data && error.data.id) ? error.data.id : error.status;
					
					vm.form.error = {
						code: error.status,
						text: error.statusText,
						message: $translate.instant('FORGOTPASSWORD.ERRORS.SERVER.' + code)
					}
				}
			);
    		
    	};
        // Methods

        //////////
    }
})();
