(function() {
	'use strict';

	angular
		.module('app.pages.application.perfilempreendedor')
		.controller('ApplicationPerfilEmpreendedorController', ApplicationPerfilEmpreendedorController);

	/** @ngInject */
	function ApplicationPerfilEmpreendedorController(
			$scope, $state, $rootScope, $anchorScroll, $location,
			$mdDialog, $translate, RestService, AuthService,
			PATH, AUTH_EVENTS) {

		// Data
		var vm = this;
		
		vm.app = null;
		vm.agree = false;
		vm.application = {
				id  : $state.params.id,
				code: $state.params.code,
		}
		
		// Methods

		// ////////

		vm.init = function() {
			if (!vm.application.id || !vm.application.code) {
				$state.go(PATH.loginState);
			}
			vm.load();
		}
		
		vm.load = function() {
			RestService.get({
				url : PATH.rest + "application/" + vm.application.id + "/" + vm.application.code,
				data: {}					
			}).then(
				function(response) {
					vm.app = response.data;
				},
				function(error) {

				}
			);
		}
		
		vm.init();
		
		var tabs = [
	        { title: 'Zero ' },
	        { title: 'One', content: "Tabs will become paginated if there isn't enough room for them."},
	        { title: 'Two', content: "You can swipe left and right on a mobile device to change tabs."},
	        { title: 'Three', content: "You can bind the selected tab via the selected attribute on the md-tabs element."},
	        { title: 'Twenty', content: "If you're still reading this, you should just go check out the API docs for tabs!"}
	      ],
	      selected = null,
	      previous = null;
		$scope.tabs = tabs;
	    vm.selectedIndex = 0;
	    
	    $scope.$watch('vm.selectedIndex', function(current, old) {
	      previous = selected;
	      selected = tabs[current];
	      if ( old + 1 && (old != current)) $log.debug('Goodbye ' + previous.title + '!');
	      if ( current + 1 )                $log.debug('Hello ' + selected.title + '!');
	    });
		
	}
})();

