(function ()
{
    'use strict';

    angular
        .module('app.principal.profile')
        .controller('PrincipalProfileController', PrincipalProfileController);

    /** @ngInject */
    function PrincipalProfileController($resource, $mdDialog, $document, $translate, $sce,
    		PATH, RestService, PostalCodeService, Converter)
    {
        var vm = this;
		var url = self.URL || self.webkitURL || self;

        vm.user = null;
        
        vm.lookupPostalCode = lookupPostalCode;
        vm.changePostalCode = changePostalCode;
        vm.newAddress = newAddress;
        vm.openPhoto = function(e) {
        	
        	var lookup = $mdDialog.show({
                controller         : 'PhotoUploadDialogController',
                controllerAs       : 'vm',
                templateUrl        : 'app/main/pages/dialogs/photo-upload/photo-upload-dialog.html',
                parent             : angular.element($document.body),
                targetEvent        : e,
                clickOutsideToClose: false,
                locals             : {
                    event          : e
                }
            }).then(function(found) {
//            	findCity(addr, found);
			});
        	
        };
        vm.init = function() {
        	
			RestService.get({
				url: PATH.rest + 'user'
			}).then(function(response) {
				vm.user = response.data;
				if (vm.user.addresses.length == 0) {
					vm.user.addresses.push(newAddress());
				}
				vm.user.cpf = null,
				vm.user.rg = null;
				
				if (vm.user.photoContent && vm.user.photoContent.bnContent) {
					var ablob = Converter.base64toBlob(
							vm.user.photoContent.bnContent,
							vm.user.photoContent.mimeType.nmMimeType
					);
					vm.userphoto = $sce.trustAsResourceUrl(        				
		    			url.createObjectURL(ablob)
		   			);
				}
				
				if (vm.user.dtBirthday) vm.user.dtBirthday = new Date(vm.user.dtBirthday);
				if (vm.user.dtDeathday) vm.user.dtDeathday = new Date(vm.user.dtDeathday);
				
			});

        };
        
        vm.save = function() {
        	
        	
        	
        }
        
        vm.init();

        vm.addressTypes = [
        	{ id: 1, name: $translate.instant('GLOBAL.ADDRESS.TYPE.HOME') },
        	{ id: 2, name: $translate.instant('GLOBAL.ADDRESS.TYPE.BUSINESS') },
        ];

        function lookupPostalCode(e, addr) {

        	var lookup = $mdDialog.show({
                controller         : 'PostalCodeSearchDialogController',
                controllerAs       : 'vm',
                templateUrl        : 'app/main/pages/dialogs/postalcode-search/postalcode-search-dialog.html',
                parent             : angular.element($document.body),
                targetEvent        : e,
                clickOutsideToClose: false,
                locals             : {
                    event          : e
                }
            }).then(function(found) {
            	findCity(addr, found);
			});
        	
        };
        
        function findCity($addr, $found) {
			$addr.city = null;
			$addr.nmPublicPlace = null;
			$addr.nmNeighborhood = null;
			
			RestService.get({ url: PATH.rest + 'geo/city/localcode/' + $found.ibge })
			.then(function(response) {
	        	$addr.nmPostalCode = $found.cep;
				$addr.city = response.data;
				$addr.nmPublicPlace = $found.logradouro;
				$addr.nmNeighborhood = $found.bairro;
			});
        }
        
        function newAddress() {
			return {
				nmUserAddress: null,
				nmPostalCode: null,
				nmPublicPlace: null,
				nmNumber: null,
				nmComplement: null,
				nmNeighborhood: null,
				city: null,
				type: vm.addressTypes[0]
			};
        }
        
        function changePostalCode($addr) {
        	var $postalcode = $addr.nmPostalCode.replace(/\D/g,'');
        	if ($postalcode.length == 8) {
        		PostalCodeService.cep($postalcode).then(function(response) {
        			var $found = response.data;
        			findCity($addr, $found);
        		});
        	}
        }
        
    }

})();

