(function ()
{
    'use strict';

    angular
        .module('app.principal.dashboard')
        .controller('PrincipalDashboardController', PrincipalDashboardController);

    /** @ngInject */
    function PrincipalDashboardController(
    		$scope,
    		$state,
    		$interval,
    		$translate,
    		$document,
    		$mdSidenav,
    		$mdDialog,
    		$mdToast,
    		Valid,
    		RestService,
    		PATH)
    {
        var vm = this;
        
        vm.documents = null;
        
   		$scope.cpfValidation = (function() {
			return {
		    	test: function(value) {
		        	return Valid.cpf(value);
		        }
		    };
		})();

        vm.invitations = {
        	profiles: null,
        	accept: function($event, $index, $o) {
        		
    			var confirm = $mdDialog.confirm()
					.title($translate.instant('GLOBAL.DIALOG.REMOVE.TITLE'))
					.textContent($translate.instant('GLOBAL.DIALOG.REMOVE.CONTENT'))
					.ariaLabel($translate.instant('GLOBAL.DIALOG.REMOVE.CONTENT'))
					.targetEvent($event)
					.clickOutsideToClose(true)
					.parent(angular.element(document.body))
					.ok($translate.instant('GLOBAL.ACCEPT'))
					.cancel($translate.instant('GLOBAL.CANCEL'));
				
				$mdDialog.show(confirm).then(function() {
					
					RestService.post({
						url: PATH.rest + 'user/invitation/' + $o.id + '/accept'
					}).then(function() {
						vm.invitations.profiles.splice($index, 1);
						$state.go('app.setup');
						
						$mdToast.show(
							$mdToast.simple()
								.textContent($translate.instant('GLOBAL.DIALOG.REMOVE.SUCCESS'))
						    );
					});

				}, function() {
					
				});	
        		
        	},
        	refuse: function($event, $index, $o) {

    			var confirm = $mdDialog.confirm()
					.title($translate.instant('GLOBAL.DIALOG.REMOVE.TITLE'))
					.textContent($translate.instant('GLOBAL.DIALOG.REMOVE.CONTENT'))
					.ariaLabel($translate.instant('GLOBAL.DIALOG.REMOVE.CONTENT'))
					.targetEvent($event)
					.clickOutsideToClose(true)
					.parent(angular.element(document.body))
					.ok($translate.instant('GLOBAL.REFUSE'))
					.cancel($translate.instant('GLOBAL.CANCEL'));
				
				$mdDialog.show(confirm).then(function() {
					
					RestService.post({
						url: PATH.rest + 'user/invitation/' + $o.id + '/refuse'
					}).then(function() {
						vm.invitations.profiles.splice($index, 1);
						$state.go('app.setup');

						$mdToast.show(
							$mdToast.simple()
								.textContent($translate.instant('GLOBAL.DIALOG.REMOVE.SUCCESS'))
						    );
					});

				}, function() {	
					
				});	
        		
        	}
        };
        
        vm.init = init;
        
        vm.informPendency = function($event, $i, c, o) {
			
            $mdDialog.show({
                controller         : 'PendencyInformDialogController',
                controllerAs       : 'vm',
                templateUrl        : 'app/main/pages/dialogs/pendency-inform/pendency-inform-dialog.html',
                parent             : angular.element($document.body),
                targetEvent        : $event,
                clickOutsideToClose: false,
                locals             : {
                    event          : $event,
                    corporate      : c,
                    pendency       : o
                }
            }).then(function(data) {
				vm.init();
			});
			
		};
        
        
        
        vm.init();
        
        function init() {
        	
            vm.documents = {
           		pendencies: null,
           		hasPendencies: false,
           		psychologist: {
                   	pendencies: null,
                   	hasPendencies: false
           		}
            };

			RestService.get({
				url: PATH.rest + 'user'
			}).then(function(response) {
				
				vm.invitations.profiles = [];
				
				var $user = response.data;
				for (var $i = 0; $i < $user.profiles.length; $i++) {
					var $p = $user.profiles[$i];
					if ($p.state.id == 2) { // only invited
						vm.invitations.profiles.push($p);
					}
				}
				
			}).catch(function(response) {
			});

			RestService.get({
				url: PATH.rest + 'user/documents/pendencies'
			}).then(function(response) {
				vm.documents.pendencies = response.data;
				
				var $pend = false;
       			for (var i = 0; !$pend && vm.documents.pendencies && i < vm.documents.pendencies.length; i++) {
       				$pend = vm.documents.pendencies[i].pendencies.length > 0;
       			} 
				
				vm.documents.hasPendencies = $pend;
				
			}).catch(function(response) {
			});

			RestService.get({
				url: PATH.rest + 'user/documents/psychologist/pendencies'
			}).then(function(response) {
				
				vm.documents.psychologist.pendencies = response.data;				
				
				var $pend = false;
       			for (var i = 0; !$pend && vm.documents.psychologist.pendencies && i < vm.documents.psychologist.pendencies.length; i++) {
       				$pend = vm.documents.psychologist.pendencies[i].pendencies.length > 0;
       			} 

				vm.documents.psychologist.hasPendencies = $pend;
				
			}).catch(function(response) {
			});

        };


    }

})();
