(function() {
	'use strict';

	angular
		.module('app.pages.home', [])
		.config(config)
		

		.directive('pwCheck', function() {
			return {
				restrict : 'A',
				require : 'ngModel',
				link : function(scope, elem, attrs, mCtrl) {
					function pwCheck(value) {
						var firstPassword = document.getElementById(attrs.pwCheck);
						mCtrl.$setValidity('pwmatch', value === firstPassword.value);
						return value;
					}
					mCtrl.$parsers.push(pwCheck);
				}
			}
		})
		.directive('uniqueEmail', function($http) {
			var toId;
			var uri = "http://127.0.0.1:8080/rest/" + "auth/";
			return {
				restrict : 'A',
				require : 'ngModel',
				link : function(scope, elem, attrs, mCtrl) {
					function uniqueEmail(value) {
						// if there was a previous attempt, stop it.
						if (toId)
							clearTimeout(toId);
	
						// start a new attempt with a delay to keep it from
						// getting too "chatty".
						toId = setTimeout(function() {
							// call to some API that returns { isValid: true } or {
							// isValid: false }
							$http.get(uri + "" + value).success(function(data) {
								// set the validity of the field
								mCtrl.$setValidity('uniqueEmail', Boolean(data));
							});
						}, 200);
						return value;
					}
					mCtrl.$parsers.push(uniqueEmail);
				}
			}
		});

	/** @ngInject */
	function config($stateProvider, $translatePartialLoaderProvider,
			msNavigationServiceProvider) {
		// State
		$stateProvider.state('app.pages_home', {
			url : '/home',
			views : {
				'main@' : {
					templateUrl : 'app/core/layouts/content-only.html',
					controller : 'MainController as vm'
				},
				'content@app.pages_home' : {
					templateUrl : 'app/main/pages/home/home.html',
					controller : 'HomeController as vm'
				}
			},
			bodyClass : 'home'
		});

		// Translation
		$translatePartialLoaderProvider.addPart('app/main/pages/home');

		// Navigation
		/*
		 * msNavigationServiceProvider.saveItem('pages.auth.home', { title :
		 * 'Home', state : 'app.pages_home', weight: 2 });
		 */
	}
})();

