(function() {
	'use strict';

	angular
		.module('app.pages.application')
		.controller('ApplicationController', ApplicationController);

	/** @ngInject */
	function ApplicationController(
			$state, $rootScope, $anchorScroll, $location,
			$mdDialog, $translate, RestService, AuthService,
			PATH, AUTH_EVENTS) {

		// Data
		var vm = this;
		
		vm.application = {
				id  : $state.params.id,
				code: $state.params.code,
		}

		// Methods

		// ////////

		vm.init = function() {
			vm.acquireApplication();
		}
		
		vm.acquireApplication = function() {
			
			RestService.get({
				url : PATH.rest + "application/" + vm.application.id + "/" + vm.application.code,
				data: {}					
			}).then(
				function(response) {
					vm.routerApplication(response.data);
				},
				function(error) {

				}
			);

		}
		
		vm.routerApplication = function(app) {
			
			var next = '';
			
			switch(app.license.licensePackage.product.id) {
				case 10: // MEMORE_V1
				case 20: // FIGURAS_V1
				case 30: // ROTAS_A_V1
				case 31: // ROTAS_B_V1
				case 32: // ROTAS_C_V1
					return;
				case 40: // PERFIL_EMPREENDEDOR
					next = 'app.pages_application_perfilempreendedor';
					break;
			}

			$state.go(next, { id: vm.application.id, code: vm.application.code });

		}
		
		vm.init();
		
	}
})();

