(function() {
	'use strict';

	angular
		.module('app.setup')
		.controller('SetupController', SetupController);

	/** @ngInject */
	function SetupController(
			$state, $anchorScroll, $location,
			msNavigationService,
			$mdDialog, $translate, RestService,
			Session, PATH, AUTH_EVENTS) {

		// Data
		var vm = this;

		// Methods

		// ////////

		vm.getProfile = getProfile;
		vm.content = '';
		vm.init = init;
		
		vm.init();
		
		var $s = angular.element('#setup');
		var $done = $translate.instant('SETUP.DONE');
		var $fail = $translate.instant('SETUP.FAIL');
		
		function init() {
			
			var $ini = Date.now();
			vm.content += '<br>' + $translate.instant('SETUP.ACQUIRING_USER');

			RestService.get({
				url: PATH.rest + 'user'
			}).then(function(response) {
				Session.user = response.data;
				vm.content += $done + ' [' + (Date.now() - $ini) + ' ms]';
				getProfile();
			}).catch(function(response) {
				vm.content += $fail + ' [' + (Date.now() - $ini) + ' ms]';
			});
			
		};
		
		function getProfile() {
			var $ini = Date.now();
			vm.content += '<br>' + $translate.instant('SETUP.ACQUIRING_PROFILE');
			var $profile = (Session.profile && Session.profile != null) ? Session.profile : null;
			if ($profile == null && Session.user.profiles.length > 0) {
				for (var i = 0; i < Session.user.profiles.length; i++) {
					if (Session.user.profiles[i].state.id == 1) {
						$profile = Session.user.profiles[i];
						break;
					}
				}
			}
						  
			Session.profile = $profile;
			if ($profile != null) {
				vm.content += '<br>:: '
					+ $translate.instant('ROLE.' + $profile.profile.id)
					+ ' @ '
					+ $profile.corporate.nmCorporate
					+ ' ';
			}

			vm.content += ' - ' + $done + ' [' + (Date.now() - $ini) + ' ms]';
			
			// check pendencies
			
			// no pendencies

			setupNavigation($profile ? $profile.profile : null);
			
			vm.content += '<br><small style="color:white;">ftwr - SelsanTech</small>';
			
			$state.go( 'app.principal_dashboard' );
		};
		
		function setupNavigation(profile) {
			
			var id = profile ? profile.id : null;

			var $ini = Date.now();
			vm.content += '<br>' + $translate.instant('SETUP.DEFINING_NAVIGATION');
			msNavigationService.saveItem('press'       , { hidden: function() { return !id || (id != 10000); }});
			msNavigationService.saveItem('reseller'    , { hidden: function() { return !id || (id != 10010); }});
			msNavigationService.saveItem('psychologist', { hidden: function() { return !id || (id != 10022 && id != 10021); }});
			msNavigationService.saveItem('clinic'      , { hidden: function() { return !id || (id != 10020 && id != 10021); }});
			vm.content += ' - ' + $done + ' [' + (Date.now() - $ini) + ' ms]';

		}

	}
})();

