(function ()
{
    'use strict';

    angular
	    .module('fuse')
		.factory('Converter', function() {
			return {
				base64toBlob: function(base64Data, contentType) {
		            contentType = contentType || '';
		            var sliceSize = 1024;
		            var byteCharacters = atob(base64Data);
		            var bytesLength = byteCharacters.length;
		            var slicesCount = Math.ceil(bytesLength / sliceSize);
		            var byteArrays = new Array(slicesCount);
		
		            for (var sliceIndex = 0; sliceIndex < slicesCount; ++sliceIndex) {
		                var begin = sliceIndex * sliceSize;
		                var end = Math.min(begin + sliceSize, bytesLength);
		
		                var bytes = new Array(end - begin);
		                for (var offset = begin, i = 0 ; offset < end; ++i, ++offset) {
		                    bytes[i] = byteCharacters[offset].charCodeAt(0);
		                }
		                byteArrays[sliceIndex] = new Uint8Array(bytes);
		            }
		            return new Blob(byteArrays, { type: contentType });
				}
			}
		})
		.factory('Valid', function() {
			return {
				email: function(v) {
					var re = /^.+@.+\..+$/;
					return re.test(v);
				},
				
				cpf: function(v) {
					var re1 = /^[0-9]{3}\.?[0-9]{3}\.?[0-9]{3}\-?[0-9]{2}$/;
					var re2 = /^[0-9]{11}$/;

					if (!re1.test(v) && !re2.test(v)) return false;

				    var cpf = v.replace(/[^\d]+/g, '');    
				    if(cpf == '') return false;
				    
				    if (cpf.length != 11) return false;
				    // Elimina CPFs invalidos conhecidos, todos números iguais    
				    for (var i = 1; i < 11; i++) {
				    	if (cpf.charAt(0) != cpf.charAt(i)) continue;
				    	if (i == 10) return false;
				    }
				    
				    // Valida 1o digito
				    var add = 0;    
				    for (i=0; i < 9; i ++) add += parseInt(cpf.charAt(i)) * (10 - i);
				    
				    var rev = 11 - (add % 11);  
				    if (rev == 10 || rev == 11) rev = 0;    
				    if (rev != parseInt(cpf.charAt(9))) return false;
				    
				    // Valida 2o digito 
				    add = 0;    
				    for (i = 0; i < 10; i ++) add += parseInt(cpf.charAt(i)) * (11 - i);
				    
				    rev = 11 - (add % 11);  
				    if (rev == 10 || rev == 11) rev = 0;    
				    return (rev == parseInt(cpf.charAt(10)));
				},
				
				cnpj: function(v) {
					
					// TODO: nao verificado
					
					var re1 = /^[0-9]{2}\.?[0-9]{3}\.?[0-9]{3}\/?[0-9]{4}\-?[0-9]{2}$/;
					var re2 = /^[0-9]{14}$/;

					if (!re1.test(v) && !re2.test(v)) return false;
					
				    var cnpj = v.replace(/[^\d]+/g, '');    
				    var numeros, digitos, soma, i, resultado, pos, tamanho;
				    
				    if (cnpj.length < 14 && cnpj.length < 15) return false;
				    for (var i = 1; i < cnpj.length; i++) {
				    	if (cnpj.charAt(0) != cnpj.charAt(i)) continue;
				    	if (i == 10) return false;
				    }

			        tamanho = cnpj.length - 2
			        numeros = cnpj.substring(0,tamanho);
			        digitos = cnpj.substring(tamanho);
			        soma = 0;
			        pos = tamanho - 7;
			        for (i = tamanho; i >= 1; i--) {
			        	soma += numeros.charAt(tamanho - i) * pos--;
			        	if (pos < 2) pos = 9;
			        }
			        resultado = soma % 11 < 2 ? 0 : 11 - soma % 11;
			        if (resultado != digitos.charAt(0)) return false;

			        tamanho = tamanho + 1;
			        numeros = cnpj.substring(0,tamanho);
			        soma = 0;
			        pos = tamanho - 7;
			        
			        for (i = tamanho; i >= 1; i--) {
			        	soma += numeros.charAt(tamanho - i) * pos--;
			        	if (pos < 2) pos = 9;
			        }
				    
			        resultado = soma % 11 < 2 ? 0 : 11 - soma % 11;
				    
			        return (resultado == digitos.charAt(1));
			        
				}
			}
		})
		.factory('Mask', function() {
			return {
				cpf: function($v) {
					var $in = $v.replace(/\D/g,'');
			        var $out = "";
			        var $consumed = 0;
			        if ($in.length > 3) {
			        	$out += $in.substring(0, 3) + ".";
			            $consumed += 3;
			        }
			        if ($in.length > 6) {
			        	$out += $in.substring(3, 6) + ".";
			            $consumed += 3;
			        }
			        if ($in.length > 9) {
			        	$out += $in.substring(6, 9) + "-";
			            $consumed += 3;
			        }
			        if ($in.length > 12) {
			        	$out += $in.substring(9, 12);
			            $consumed += 2;
			        }
			        if ($in.length > $consumed) {
			            $out += $in.substring($consumed, $in.length);
			        }
			        return $out;
				}
			}
		})
		.factory('PostalCodeService', function($http) {
			return {
				cep: function($cep) {
					return $http({
						method: 'GET',
						url: 'https://viacep.com.br/ws/' + ($cep.replace(/\D/g,'')) + '/json/'
					});
				},
				lookup: function($state, $city, $publicplace) {
					return $http({
						method: 'GET',
						url: 'https://viacep.com.br/ws/' + $state + '/' + $city + '/' + $publicplace + '/json'
					});
				}
			}
		})
		;

})();

(function ()
{
    'use strict';

    angular
	    .module('fuse')
	    .directive('ngEnter', function() {
	        return function(scope, element, attrs) {
	        	element.bind("keydown keypress", function(event) {
	        		if(event.which === 13) {
	        			scope.$apply(function(){
	        				scope.$eval(attrs.ngEnter, {'event': event});
	        			});
	
	        			event.preventDefault();
	        		}
	        	});
	        };
	    })
	    .directive('decimals', [
	    	function () {
	    		return {
	    			require: 'ngModel',
	    			link: function($scope, element, attrs, model) {
	    				element.bind('blur', function () {
	    					var decimals = parseInt(attrs.decimals),
	    					value    = (!isNaN(element.val()))
	    						? parseFloat(element.val()).toFixed(decimals)
	    						: (0).toFixed(decimals);

	    					element.val(value);
	    					$scope.$apply(function() {
	    						model.$setViewValue(value);
	    					});
	    				});
	    			}
	    		};
	    	}
	    ])
	    .directive('maskCpf', function (Mask, Valid) {
	    		return {
	    			require: 'ngModel',
	    			link: function($scope, element, attrs, model) {
	    				model.$parsers.push(function () {
	    					
		    				var i = element.val().trim();
		    				if (i.length > 0 && i.match(/^\d/)) {
		    					
		    					element.val(Mask.cpf(i));
		    					model.$setValidity('maskCpf', Valid.cpf(i));
		    					
		    				}
	    					
	    				});
	    			}
	    		};
	    	})
		;

})();

